import { Component, OnInit, ViewChild, ElementRef, HostListener } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit  {
  @ViewChild('canvas')
  canvasRef: ElementRef;

  private triangles: Array<Triangle>;
  private minAlpha = 0.5;
  private sectionHeight = 1000;

  ngOnInit() {
    const canvas = this.canvasRef.nativeElement;
    canvas.style.width = '100%';
    canvas.style.height = '100%';
    canvas.width  = canvas.offsetWidth;
    canvas.height = canvas.offsetHeight;

    const ctx: CanvasRenderingContext2D = canvas.getContext('2d');
    ctx.globalCompositeOperation = 'overlay';

    // Draw the clip path that will mask everything else
    this.getTriangles();
    this.makeTriangles(1);
  }

  redirect(i) {
    const y = this.sectionHeight * i;
    this.scrollToY(y, Math.abs(y - window.pageYOffset) / 2, 1);
  }

  scrollToY (y, half, increment) {
    const diff = window.pageYOffset - y;
    const cdiff = Math.abs(diff);

    if (cdiff !== 0) {
      // const inc = Math.min(cdiff, increment);
      const sign = diff > 0 ? -1 : 1;

      setTimeout(() => {
        window.scrollTo(0, window.pageYOffset + increment * sign);
        if (cdiff < half)
          this.scrollToY(y, half, Math.max(increment - 1, 1));
        else
          this.scrollToY(y, half, increment + 1);
      }, 20);
    }
  }

  @HostListener('window:scroll', ['$event'])
  onScrollEvent($event) {
    const canvas = this.canvasRef.nativeElement;
    const ctx: CanvasRenderingContext2D = canvas.getContext('2d');
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    const alpha = Math.max((window.outerHeight - window.pageYOffset) / window.outerHeight, this.minAlpha)
    this.makeTriangles(alpha);
  }

  private makeTriangles(alpha) {
    for (let i = 0; i < this.triangles.length; i++) {
      this.makeTriangle(this.triangles[i], alpha);
    }
  }

  private makeTriangle(triangle: Triangle, alpha) {
    const canvas = this.canvasRef.nativeElement;
    const ctx: CanvasRenderingContext2D = canvas.getContext('2d');

    ctx.globalAlpha = alpha;

    ctx.fillStyle = triangle.color;
    ctx.beginPath();

    ctx.moveTo(triangle.vs[0].x, triangle.vs[0].y);
    for (let i = 1; i < triangle.vs.length; i++) {
      ctx.lineTo(triangle.vs[i].x, triangle.vs[i].y);
    }
    ctx.lineTo(triangle.vs[0].x, triangle.vs[0].y);

    ctx.closePath();
    ctx.fill();
  }

  private getTriangles() {
    this.triangles = new Array<Triangle>();
    const amount = 100;
    for (let i = 0; i < amount; i++) {
      const tri: Triangle = this.getTriangle();
      this.triangles.push(tri);
    }
  }

  private getTriangle(): Triangle {
    const tri: Triangle = new Triangle();

    const canvas = this.canvasRef.nativeElement;

    const stepw = Math.max(canvas.width, canvas.height) / 5;
    const steph = stepw;

    const coordinate: Vertex = new Vertex(
      Math.floor((Math.random() * canvas.width) + 1),
      Math.floor((Math.random() * canvas.height) + 1));

    tri.vs.push(coordinate);
    tri.vs.push(new Vertex(coordinate.x + this.randomInt(0, stepw), coordinate.y + this.randomInt(0, steph)));
    tri.vs.push(new Vertex(coordinate.x + this.randomInt(0, stepw), coordinate.y + this.randomInt(0, steph)));

    // const colors = ['rgb(214, 144, 239)', 'rgb(119, 197, 219)'];
    const colors = ['rgb(240,240,240)', 'rgb(235,235,235)', 'rgb(230,230,230)', 'rgb(225,225,225)'];
    tri.color = colors[this.randomInt(0, colors.length - 1)];

    return tri;
  }

  private randomInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }
}

class Triangle {
  public vs: Array<Vertex>;
  public color: string;

  constructor() {
    this.vs = new Array<Vertex>();
  }
}

class Vertex {
  public x: number;
  public y: number;

  constructor(x, y) {
    this.x = x;
    this.y = y;
  }
}



